import { useState, useEffect } from 'react'
import { inject, observer } from 'mobx-react';
import apiProxy from '../../model/ApiProxy'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import UnderlineText from '../../components/UnderlineText/UnderlineText'
import { formatAddress } from '../../utils/utils'
import {bg} from '../../lib/web3js/index'
import 'rc-pagination/assets/index.less';
import 'rc-select/assets/index.less';
import deirLogo from './img/logo-deri.svg'
import IPagination from '../../components/Pagination/IPagination'
import './practice.less'
function Practice({ lang, wallet }) {
  const [yourPnl, setYourPnl] = useState();
  const [page, setPage] = useState(1)
  const [pnlList, setPnlList] = useState([])
  const [showPnlList, setShowPnlList] = useState([])
  const onPage = (page) => {
    setPage(page)
  }
  const getYourPnl = async () => {
    let res = await apiProxy.request("getUserEarnInfo", [wallet.detail.account, 2])
    if (res) {
      let pnl = Math.abs(res.pnl)  < 10**-10 ? 0 : (res.pnl - 1) * 100
      setYourPnl(pnl)
    }
  }
  const getEarnTopUsers = async () => {
    let res = await apiProxy.request('getEarnTop50Users', [2])
    if (res) {
      let arr = res.map(item => {
        item.pnl =  Math.abs(item.pnl)  < 10**-10 ? 0 : (item.pnl - 1) * 100;
        item.account = formatAddress(item.account)
        return item
      })
      setPnlList(arr)
    }
  }
  useEffect(() => {
    let arr
    if (page === 1) {
      arr = pnlList.slice(0, page * 10)
    } else {
      arr = pnlList.slice((page - 1) * 10, page * 10)
    }
    setShowPnlList(arr)
  }, [pnlList, page])
  useEffect(() => {
    if (wallet.isConnected()) {
      getYourPnl()
    }
    getEarnTopUsers()
  }, [wallet.detail, wallet])
  return (
    <div className='testnet-practice'>
      <div className='practice-info'>
        <div className='practice-title'>
          {lang['practice-to-earn']}
        </div>
        <div className='practice-time'>
          {/* {lang['time']} */}
          Mar. 1st 09:00 AM (UTC) — Mar. 8th 09:00 AM (UTC)
        </div>
        <div className='practice-info-box'>
          <div className='practice-info-left'>
            <div className='total-rewards-pool'>
              <div className='total-rewards-box'>
                <div className='deri-logo'>
                  <img src={deirLogo} alt='deri' />
                </div>
                <div className='total-rewards-num'>
                  <div className='total-rewards-pool-title'>
                    {lang['total-rewards-pool']}
                  </div>
                  <div className='toatl-rewards-deri'>
                    <span>
                      10,000
                    </span>
                    <span className='deri-text'>
                      DERI
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className='your-pnl'>
              <span>
                <UnderlineText text={lang['your-pnl']} tip={lang['your-pnl-tip']} multiline={true} />
              </span>
              <span className='your-pnl-pen'>
                <DeriNumberFormat value={yourPnl} suffix='%' decimalScale={2} />
              </span>
            </div>
          </div>
          <div className='practice-info-right'>
            <div className='top-pnl-list-box'>
              <div className='top-pnl-list-box-title'>
                {/* {lang['top-pnl']} */}
                Leaderboard
              </div>
              <div className='top-pnl-list'>
                <div className='top-pnl-list-title'>
                  <span>{lang['no']}</span>
                  <span>{lang['user-addr']}</span>
                  <span>{lang['pnl']}</span>
                </div>
                <div className='top-pnl-list-info'>
                  {showPnlList.map((item, index) => {
                    return (
                      <div className='top-pnl-list-info-box' key={index}>
                        <div className='no'>{item.index}</div>
                        <div className='address'>{item.account}</div>
                        <div className='pnl'>
                          <DeriNumberFormat value={item.pnl} suffix='%' decimalScale={2} />
                        </div>
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
            <div className='page-box'>
              <IPagination page={page} onPage={onPage} count={pnlList.length} pageSize={10} />
                <div className='remind'>* {lang['update-every-hour']}</div>
            </div>
          </div>
        </div>
      </div>
      <div className='detailed-rules'>
        <a target="_blank" href="https://theontoteam.medium.com/1b958bc5228a" rel='noreferrer' >
          {lang['detailed-rules']}
        </a>
      </div>
    </div>
  )
}
export default inject('wallet')(observer(Practice))